@import "node_modules/@wordpress/base-styles/_colors.scss"; @import "node_modules/@wordpress/base-styles/_variables.scss"; @import "node_modules/@wordpress/base-styles/_mixins.scss"; @import "node_modules/@wordpress/base-styles/_breakpoints.scss"; @import "node_modules/@wordpress/base-styles/_animations.scss"; @import "node_modules/@wordpress/base-styles/_z-index.scss"; @import "_colors"; @import "_breakpoints"; @import "_mixins"; 
.progressbar {
	display: flex;
	max-width: 600px;

	.progressbar__container {
		flex-grow: 1;
		display: flex;
		flex-flow: wrap;
		margin: 8px 8px 8px 0;
		background-color: $white;
		height: 32px;
		border: 1px $dark-gray-900 solid;
		border-radius: 4px;
	}

	.progressbar__inner {
		background-color: #007CBA;
		display: inline-flex;
		flex-direction: row-reverse;
		height: 100%;
	}

	.progressbar__outer-progress-label {
		align-self: center;
		margin: 4px;
	}

	.progressbar__inner-progress-label {
		color: $white;
		align-self: center;
		margin: 4px;
	}

	.progressbar__total-label {
		align-self: center;
	}
}
